export default {
    data() {
        return {
            wsurl: undefined,
            rm: undefined
        }
    },
    methods: {
        /**
         * 关闭websocket时
         * */
        closeSocket: function () {
            let that = this;
            // this.ws=undefined;
            clearTimeout(this.intval);
            setTimeout(function () {
                that.socketInit(that.wsurl, that.rm);
            }, 300);
            // this.socketInit();
        },

        /**
         * 初始化websocket
         * */
        socketInit: function (wsUrl, readMessage) {
            this.wsurl = wsUrl;
            this.rm = readMessage;
            this.ws = new WebSocket(wsUrl);
            this.ws.onmessage = readMessage;
            this.ws.onclose = this.closeSocket;
            let that = this
            this.intval = setInterval(() => {
                that.ws.send("1")
            }, 10 * 1000)
        },
        sendToClose: function () {
            this.ws.close();
            // this.ws.send("close it !!");
        }
    },
}